.Button_Button__3R1xi {
  margin: 30px;
  display: inline-block;
}
button {
  width: 100%;
  height: 70px;
  border-radius: 10px;
  background-color: rgb(255, 153, 0);
  padding: 2%;
  color: white;
  font-size: large;
  font-style: italic;
  outline: none;
  cursor: pointer;
  -webkit-transition: 0.5s;
  transition: 0.5s;
}
button:hover {
  background-color: rgb(153, 94, 6);
}
button:disabled {
  background-color: #ccc;
  cursor: not-allowed;
}

