.Pictures {
  width: 26%;
  padding: 2%;
  padding-bottom: 1%;
  border: 1px solid black;
  display: inline-block;
  box-shadow: 3px 3px 5px 6px #ccc;
  background-color: green;
  border-radius: 30px;
}

@media (max-width: 1400px) {
  .Pictures {
    width: 30%;
    padding: 2%;
    padding-bottom: 1%;
  }
}

@media (max-width: 1100px) {
  .Pictures {
    width: 40%;
    padding: 2%;
    padding-bottom: 1%;
    display: block;
  }
}

@media (max-width: 800px) {
  .Pictures {
    width: 48%;
    padding: 3%;
    padding-bottom: 1%;
    display: block;
  }
}

@media (max-width: 620px) {
  .Pictures {
    width: 57%;
    padding: 3%;
    padding-bottom: 1%;
    display: block;
  }
}

@media (max-width: 440px) {
  .Pictures {
    width: 64%;
    padding: 4%;
    padding-bottom: 1%;
    display: block;
  }
}

